import React from "react";
import { HashRouter as Router, Route, Switch } from "react-router-dom";
import "./App.scss";

import Main from "./containers/Main";
import DataAnalystAcademics from "./containers/academics/DataAnalystAcademics";

// Ensure scroll to top on route change
function ScrollToTop() {
  React.useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  return null;
}

function App() {
  return (
    <Router>
      <Switch>
        <Route exact path="/">
          <ScrollToTop />
          <Main />
        </Route>
        <Route path="/data-analyst-academics">
          <ScrollToTop />
          <DataAnalystAcademics />
        </Route>
      </Switch>
    </Router>
  );
}

export default App;
